














































export default {
	data() {
		return {
			articles:[]
		}
	},
	onLoad:function() {
		this.getArticles();
	},
	onShow: function() {
		var _this = this;
		const loginKey = uni.getStorageSync('login_key');
		if (loginKey) {
			//console.log(loginKey);
			this.storageData = {
				login: loginKey.login,
				login: loginKey.nickname
			}
		} else {
			this.storageData = {
				login: false
			};
		}
		uni.request({
			url: 'http://192.168.43.26:8080/api/user/' + uni.getStorageSync('login_key').userId,
			method: 'GET',
			header: { 'content-type': 'application/json' },
			success: res => {
				if (res.data.code === 0) {
					console.log(res.data.data.avatar+'————————————');
					_this.avatar = res.data.data.avatar;
					_this.nickname = res.data.data.nickname;
				}
			}
		});
	},
	onPullDownRefresh:function(){
		this.getArticles();
	},
	methods: {
		getArticles:function(){
			var _this = this;
			uni.request({
				url: this.apiServer + '/article/list',
				method:'GET',
				header:{'content-type': 'application/x-www-form-urlencdded'},
				success: res => {
					_this.articles = res.data.data;
				},
				complete:function(){
					uni.stopPullDownRefresh();
				}
			});
		},
		gotoDetail:function(aId){
			uni.navigateTo({
				url:'../article_detail/article_detail?aId=' +aId
			});
		},
		gotoWrite:function(){
			if(uni.getStorageSync('login_key').login === true){
			uni.navigateTo({
				url:'../write/write'
			});
			}else{
				uni.navigateTo({
					url:'../signin/signin'
				});
			}
		},
		handleTime: function(date) {
			var d = new Date(date);
			var year = d.getFullYear();
			var month = d.getMonth() + 1;
			var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate();
			var hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours();
			var minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes();
			var seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds();
			return year + '-' + month + '-' + day + ' ' + hour + ':' + minutes + ':' + seconds;
		},
		handleContent:function(content){
			content = content.replace(/(\n)/g,'');
			content = content.replace(/(\t)/g,'');
			content = content.replace(/(\r)/g,'');
			content = content.replace(/<\/?[^>]*>/g,'');
			content = content.replace(/\s*/g,'');
			return content.substring(0,50);
		}
	}
};
